<template>
  <div class="wrapper">
    <div class="container">
      <div
        class="card"
        v-for="item in cardData"
        :key="item.value"
        @click="isDialog(item.label, item.value)"
      >
        <p>{{ item.title }}</p>
      </div>
    </div>
    <el-dialog :title="windowTitle" :visible.sync="dialogVisible" width="60%">
      <div class="resourcesPage" v-show="resources">
        <Resources></Resources>
      </div>
      <div v-show="!resources">豆瓣主页正在开发中</div>
    </el-dialog>
  </div>
</template>

<script>
import Resources from "@/components/projects/Resources.vue";
export default {
  name: "",
  components: {
    Resources,
  },
  props: {},
  data() {
    return {
      dialogVisible: false,
      windowTitle: "",
      resources: false,
      cardData: [
        {
          title: "我的豆瓣",
          label: "豆瓣",
          value: "douban",
        },
        {
          title: "前端资源页",
          label: "前端资源",
          value: "font-end",
        },
      ],
    };
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    click() {
      // 创建script标签，引入外部文件
      let script = document.createElement("script");
      script.type = "text/javascript";
      script.src =
        "http://www.douban.com/service/badge/149924582/?selection=latest&amp;picsize=small&amp;show=collection&amp;n=8&amp;columns=2";
      document.getElementsByTagName("body")[0].appendChild(script);
      // 引入成功
      script.onload = function () {
        console.log("js资源已加载成功了");
      };
      // 引入失败
      script.onerror = function () {
        console.log("js资源加载失败了");
      };
    },
    isDialog(label, value) {
      this.dialogVisible = true;
      this.windowTitle = label;
      if (value === "font-end") {
        this.resources = true;
      } else {
        this.resources = false;
      }
    },
  },
};
</script>

